import React from "react";
import { Zinfo } from "zerod";
import { Tag } from "antd";
import HOC from "@/HOC/load-HOC.js";
const { AmdDocHOC } = HOC;
import doc from "zerod/components/Zinfo/doc.md";
export default AmdDocHOC(doc, {
	demo1: function() {
		class Myjavascript extends React.PureComponent {
			items = [
				{
					key: "serviceCode",
					label: "服务编码",
					render: () => {
						return new Promise((resolve) => {
							setTimeout(() => {
								resolve((value, record) => {
									return <Tag color="red">{value}</Tag>;
								});
							}, 5000);
						});
					},
				},
				{
					key: "serviceName",
					label: "服务名称",
				},
			];
			state = {
				data: {
					serviceCode: "IDDKKDSSDSD",
					serviceName: "测试治大国",
				},
			};
			render() {
				return <Zinfo items={this.items} fieldValue={this.state.data} />;
			}
		}
		return <Myjavascript />;
	},
});
